<template>
	<view>
		<uni-popup v-if="from === 'find'" ref="popup" type="dialog">
			<uni-popup-dialog type="info" mode="input" title="‰Ω†‰ºöÊÄé‰πàÊâìÊãõÂëº" maxlength="35" placeholder="35Â≠óÂºïËµ∑ÂØπÊñπÁöÑÊ≥®ÊÑè" :before-close="true" @close="close" @confirm="confirm"></uni-popup-dialog>
		</uni-popup>
		<view v-if="from==='friendReq'" class="reqButton">
				<button @tap="acceptReq" :disabled="!reqIng" type="primary">ü§ù‰Ω†Â•ΩÂëÄ</button>
			  <button @tap="refuseReq" :disabled="!reqIng" type="warn">üëãÊãíÁªù</button>
				 <button @tap="sendMsg" :disabled="reqIng" type="primary">üéâÂèëÊ∂àÊÅØ</button>
		</view>
		<userCenter :userInfo="peopleInfo" type="other" v-on:likeU="handleLike">
			<template #editor>
				<view v-if="from === 'find'">
					<view v-if="!urFriend" class="editInfo stranger" @tap="addFriend">
						<uni-icons type="personadd" size="17" color="hotpink"></uni-icons>
						<text>Êâì‰∏™ÊãõÂëº</text>
					</view>
					<view v-if="urFriend" class="editInfo friend" @tap="delFriend">
						<uni-icons type="minus" size="17" color="red"></uni-icons>
						<text>Êñ≠ÁªùÂÖ≥Á≥ª</text>
					</view>
				</view>
			</template>
		</userCenter>
	</view>
</template>

<script>
const db = wx.cloud.database();
import { mapState,mapMutations } from 'vuex';
import uniPopupDialog from '../../components/uni-popup/uni-popup-dialog.vue';
export default {
	components: {
		uniPopupDialog
	},
	data() {
		return {
			peopleInfo: {},
			urFriend: false,
			from: '',
			reqIng:true
		};
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		...mapMutations(['update']),
		acceptReq(){
			uni.showLoading({
				title:'ËØ∑Á®çÂêé'
			})
			console.log('Êé•Âèó')
			wx.cloud.callFunction({
				name:'acceptReq',
				data:{
					openid:this.peopleInfo._openid
				}
			}).then(res=>{
				console.log(res)
					// Êõ¥Êñ∞Êú¨Âú∞friendlist
					// this.update({
					// 	friend:newList
					// })
					this.reqIng=false
			}).then(()=>{
				uni.hideLoading()
			})
		},
		refuseReq(){
			console.log('ÊãíÁªù')
			wx.cloud.callFunction({
				name:'refuseReq',
				data:{
					openid:this.peopleInfo._openid
				}
			}).then(res=>{
				console.log(res)
				if(res.result.stats.updated===1){
					uni.navigateBack()
				}
			})
		},
		close(done) {
			done();
		},
		confirm(done, value) {
			console.log(value);
			if (!value) {
				uni.showToast({
					title: 'ÂøÖÈ°ªÂ∏¶‰∏ä‰∏ÄÂè•ËØùÂì¶'
				});
				return;
			}
			wx.cloud
				.callFunction({
					name: 'addFriend',
					data: {
						_openid: this.peopleInfo._openid,
						words: value
					}
				})
				.then(res => {
					console.log(res);
					if (res.result === 'requested') {
						uni.showToast({
							title: 'Â∑≤ÁªèÁÇπËøá‰∫ÜÂì¶',
							icon: 'none',
							duration: 2000
						});
						return;
					}

					uni.showToast({
						title: 'ËØ∑ËÄêÂøÉÁ≠âÂæÖÂØπÊñπÁöÑÂõûÂ∫îÂêß',
						icon: 'none'
					});
				});
			done();
		},
		handleLike() {
			if (!this.userInfo._openid) {
				uni.showToast({
					title: 'ÊÇ®ËøòÊ≤°ÊúâÁôªÂΩï',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			console.log('Â§ÑÁêÜÁÇπËµû');
			wx.cloud
				.callFunction({
					name: 'addLike',
					data: {
						target: this.peopleInfo._openid
					}
				})
				.then(res => {
					console.log(res);
					if (res.result === 'already') {
						uni.showToast({
							title: '‰Ω†Â∑≤ÁªèlikeËøá‰ªñ‰∫Ü',
							icon: 'none'
						});
					}
					this.peopleInfo.like++;
				});
		},

		addFriend() {
			if (!this.userInfo._openid) {
				uni.showToast({
					title: 'ÊÇ®ËøòÊ≤°ÊúâÁôªÂΩï',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			console.log(`Âêë${this.peopleInfo._openid}ÂèëÈÄÅÂ•ΩÂèãÁî≥ËØ∑`);
			this.$refs.popup.open();
		},
		delFriend() {
			uni.showModal({
				content:'Á°ÆËÆ§ÁßªÈô§Â•ΩÂèãÂêóÔºü',
				cancelText:'ÂÜçÊÉ≥ÊÉ≥',
				confirmText:'ÊãúÊãúÂíØ',
				confirmColor:'red',	
				success: () => {
						console.log(`Âà†Èô§${this.peopleInfo.nickName}`)
						wx.cloud.callFunction({
							name:'delFriend',
							data:{
								openid:this.peopleInfo._openid
							}
						}).then(res=>{
							console.log(res.result.stats.updated)
							if(res.result.stats.updated===1){
								console.log('ÊàêÂäüÂà†Èô§')
							}else{
								uni.showToast({
									icon:'info',
									title:'ÊúçÂä°Âô®ÂøôËØ∑Á®çÂêéÂÜçËØï'
								})
							}
						})
				}
			})
		}
	},
	onLoad(options) {
		console.log(`Áî±È°µÈù¢${options.from}ËøõÂÖ•${options.openid}`);
		this.from = options.from;
		// Âà§Êñ≠‰ªéfindÁÇπÂáªËøõÊù•ËøòÊòØÊâìÊãõÂëºÁÇπËøõÊù•

		uni.showLoading({
			title: 'Âä†ËΩΩ‰∏≠'
		});
		const openid = options.openid;
		db.collection('users')
			.where({
				_openid: openid
			})
			.get()
			.then(res => {
				this.peopleInfo = res.data[0];
				uni.setNavigationBarTitle({
					title:this.peopleInfo.nickName+'ÁöÑ‰∏ªÈ°µ'
				})
				if (this.userInfo.friend.includes(this.peopleInfo._openid)) {
					this.urFriend = true;
				}
			})
			.catch(err => {
				console.log(err);
			})
			.then(() => {
				uni.hideLoading();
			});
	}
};
</script>

